
import { makeMap, no } from '../../shared/utils'
// Regular Expressions for parsing tags and attributes
// NOTE æ ‡ç­¾å
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`
// NOTE æ ‡ç­¾åæ•è· é€šè¿‡æ­£åˆ™çš„åˆ†ç»„æŸ¥æ‰¾
const qnameCapture = `((?:${ncname}\\:)?${ncname})`
// NOTE åŒ¹é…å¼€å§‹æ ‡ç­¾
const startTagOpen = new RegExp(`^<${qnameCapture}`)
// NOTE åŒ¹é…æ•´ä¸ªæ ‡ç­¾çš„å…³é—­
const startTagClose = /^\s*(\/?)>/
// NOTE åŒ¹é…ç»“æŸæ ‡ç­¾
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`)
// NOTE åŒ¹é…å±æ€§
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/
const dynamicArgAttribute = /^\s*((?:v-[\w-]+:|@|:|#)\[[^=]+\][^\s"'<>\/=]*)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/
const doctype = /^<!DOCTYPE [^>]+>/i
const comment = /^<!\--/
const conditionalComment = /^<!\[/
// NOTE åŒ¹é… {{ }}
const defaultTagRe = /\{\{((?:.|\r?\n)+?)\}\}/g
export const isPlainTextElement = makeMap('script,style,textarea', true)
export const isNonPhrasingTag = makeMap(
  'address,article,aside,base,blockquote,body,caption,col,colgroup,dd,' +
    'details,dialog,div,dl,dt,fieldset,figcaption,figure,footer,form,' +
    'h1,h2,h3,h4,h5,h6,head,header,hgroup,hr,html,legend,li,menuitem,meta,' +
    'optgroup,option,param,rp,rt,source,style,summary,tbody,td,tfoot,th,thead,' +
    'title,tr,track'
)
const decodingMap = {
  '&lt;': '<',
  '&gt;': '>',
  '&quot;': '"',
  '&amp;': '&',
  '&#10;': '\n',
  '&#9;': '\t',
  '&#39;': "'"
}
const encodedAttr = /&(?:lt|gt|quot|amp|#39);/g
const encodedAttrWithNewLines = /&(?:lt|gt|quot|amp|#39|#10|#9);/g
const isIgnoreNewlineTag = makeMap('pre,textarea', true)
const shouldIgnoreFirstNewline = (tag, html) => tag && isIgnoreNewlineTag(tag) && html[0] === '\n'
function decodeAttr(value, shouldDecodeNewlines) {
  const re = shouldDecodeNewlines ? encodedAttrWithNewLines : encodedAttr
  return value.replace(re, match => decodingMap[match])
}




export function parseHTML(html) {
  const ELEMENT_TYPE = 1;
  const TEXT_TYPE = 3;
  const stack = [];
  let currentParent; // æŒ‡å‘çš„æ˜¯æ ˆä¸­çš„æœ€åä¸€ä¸ª
  let root;
  function createASTElement(tag, attrs) {
    return {
      tag,
      type: ELEMENT_TYPE,
      children: [],
      attrs,
      parent: null
    }
  }
  function start(tag, attrs) {
    let node = createASTElement(tag, attrs); // åˆ›é€ ä¸€ä¸ªastèŠ‚ç‚¹
    if (!root) {   // çœ‹ä¸€ä¸‹æ˜¯å¦ä¸ºç©ºæ ‘
      root = node; // å¦‚æœä¸ºç©ºåˆ™å½“å‰æ˜¯æ ‘çš„æ ¹èŠ‚ç‚¹
    }
    if (currentParent) {
      node.parent = currentParent; // åªèµ‹äºˆäº†parentå±æ€§
      currentParent.children.push(node); // è¿˜éœ€è¦è®©çˆ¶äº²è®°ä½è‡ªå·±
    }
    stack.push(node)
    currentParent = node;

    // console.log('root', root)
  }
  function chars(text) {
    text = text.replace(/\s/g, '');
    if (text) {
      currentParent.children.push({
        type: TEXT_TYPE,
        text,
        parent: currentParent
      })
    }
  }
  function end(tag) {
    let node = stack.pop();
    currentParent = stack[stack.length - 1];
  }

  function advance(n) {
    html = html.substring(n);
  }
  function parseStartTag() {
    const start = html.match(startTagOpen);
    if (start) {
      const match = {
        tagName: start[1], // æ ‡ç­¾å
        attrs: []
      }
      advance(start[0].length)

      // å¦‚æœä¸æ˜¯å¼€å§‹æ ‡ç­¾çš„ç»“æŸ å°±ä¸€ç›´åŒ¹é…ä¸‹å»
      let attr, end;
      while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
        advance(attr[0].length)
        match.attrs.push({
          name: attr[1],
          value: attr[3] || attr[4] || attr[5]
        })
      }
      if (end) {
        advance(end[0].length)
        // console.log('attr[0].length: ', attr[0].length);
      }
      return match;
    }

    // console.log(html)
    return false;
  }

  while (html) { // htmlæœ€å¼€å§‹è‚¯å®šæ˜¯ä¸€ä¸ª <   <div>hello</div>
    // å¦‚æœtextEnd ä¸º0 è¯´æ˜æ˜¯ä¸€ä¸ªå¼€å§‹æ ‡ç­¾æˆ–è€…ç»“æŸæ ‡ç­¾
    // å¦‚æœtextEnd >0 è¯´æ˜å°±æ˜¯æ–‡æœ¬çš„ç»“æŸä½ç½®
    let textEnd = html.indexOf('<'); // å¦‚æœindexOfä¸­çš„ç´¢å¼•æ˜¯0 åˆ™è¯´æ˜æ˜¯ä¸ªæ ‡ç­¾
    if (textEnd === 0) {
      const startTagMatch = parseStartTag();
      if (startTagMatch) {
        start(startTagMatch.tagName, startTagMatch.attrs)
        continue;
      }
      let endTagMatch = html.match(endTag)
      if (endTagMatch) {
        advance(endTagMatch[0].length);
        end(endTagMatch[1])
        continue;
      }
    }
    if (textEnd > 0) {
      let text = html.substring(0, textEnd); // æ–‡æœ¬å†…å®¹
      if (text) {
        chars(text)
        advance(text.length)
      }
    }

  }
  // console.log('cc', html)
  return root;
}